body {
  padding: 0 !important;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
  text-transform: capitalize !important;
}

.grid-container {
  display: grid;
  width: 100%;
  height: 100vh;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 1fr;
}

.grid-header {
  grid-row: 1;
  grid-column: 1;
}

.grid-body {
  grid-row: 2;
  grid-column: 1;
  /* overflow-y: scroll; */
}

.grid-footer {
  grid-row: 3;
  grid-column: 1;
}
