  @media screen and (max-width: 768px) {
    table {
      border: 0;
    }
  
    table caption {
      font-size: 1.3em;
    }
    
    table thead {
      border: none;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px;
    }
    
    table tr {
      border-bottom: 3px solid #ddd;
      display: block;
      margin-bottom: .625em;
    }
    
    table td {
      border-bottom: 1px solid #ddd;
      display: block;
    
      text-align: center;
    }
    
    table td::before {
  
      content: attr(data-label);
      float: left;
      font-weight: bold;
  
    }
    
    table td:last-child {
      border-bottom: 0;
    }
  }
